      <pre>
《以太坊大富翁》游戏规则
 
《以太坊大富翁》是根据风靡全球100多年的《大富翁》游戏改编，基于以太坊钱包开发的财智游戏。游戏代码写在以太坊公链上，具有公开透明、杜绝人为操控、不可篡改的特点，游戏的所有数据公开、代码开源、简单安全、绝对公平，真正做到去中心化。
 
【玩家资产】
买地建楼投入资产分为（V1平房、V2别墅、V3城堡）共三个玩家级别。
V1级别：1-5 ETH；V2级别：6-10 ETH；V3级别：11-15 ETH。
说明：
1. 玩家的钱包里有相应数额的ETH，点击“申请土地”即视为资产投入，进入AI智能排队审核环节。审核通过，启动玩家资产释放、相对应级别的建楼收益及推荐收益的奖励。
2. 玩家资产每天1%释放，玩家可随时提释放的资产，但玩家释放资产未100%释放时提取，提取时即视为退出本轮游戏，玩家不再享有余下的资产释放及所有收益资格。
 
【建楼收益】
智能合约按各钱包账户投入的玩家资产数额，获得建楼收益：
V1级别每天0.5%；V2级别每天0.7%；V3级别每天1.0%。
说明：
   建楼收益是根据玩家资产计算，每天自动分配到玩家钱包，玩家可自由提取支配。
 
【推荐收益】
    邀约玩家参与游戏，可享受推荐收益，推荐收益为邀约的玩家建楼收益的百分比：
1、V1获直接推荐玩家（即第1代）的50%；
2、V2获第1代玩家的70%，第2代玩家的50%；
3、V3直接推荐一人可获无限代推荐收益：
    第1代玩家建楼收益的100%；第2代玩家建楼收益的70%；第3代玩家建楼收益的50%；第4-10代玩家建楼收益的10%；第11-20代玩家建楼收益的5%；第21代以上1%
说明：
1、如果【推荐的玩家及伞下的团队投入的资产>推荐人的投入资产】时，推荐人获得的推荐收益则按自己投入的资产计算。
2、当【建楼收益+推荐收益=玩家资产】时，玩家资产即100%释放，本轮游戏结束。
3、一轮游戏结束后，玩家可提币再申请土地，审核通过后持续享有建楼收益和推荐收益。
 
【3倍彩蛋基金说明】
   游戏中，玩家的建楼收益和推荐收益的10%转入彩蛋基金钱包，当游戏的合约总资产为零，智能合约即启动彩蛋基金计划，分配给最后一轮进入游戏的玩家。
1、第一笔彩蛋基金：分配给最后倒数5名的玩家，获得投入资产的3倍彩蛋基金（ETH）。
2、第二笔彩蛋基金：剩余的玩家会用3倍ERM代币补充。（ERM代币将来在交易所交易，也是启动下一轮游戏的通证）
      </pre>
